:root {
	--principal-color: #5987AA;
	--second-color: #ffffff;
	--other-color: #1c1c1c;
	--principal-color-text: #000000;
	--principal-color-hover: #fb0000;
	--principal-font: Galeria light;
}

*{
	padding: 0;
	margin: 0;
}
body{
	font-family: Galeria Bold;
	background-color: #f4f4f4;
}
a{
	font-size: 1.8rem;
	text-decoration: none;
}
section{
	text-align: center;
	align-items: center;
	margin-top: 100px;
}
.logo{
	font-family: Galeria light;
	transition: .1s ease;
	font-size: 30px;
	margin-top: 16px;
	margin-left: 2%;
}
#logo{
	visibility: visible;
	transition: .5s;
}
#logoMobile{
	text-align: end;
	visibility: hidden;
}
.navbar{
	border-bottom: 6px solid #1c1c1c;
	overflow: hidden;
	min-height: 60px;
	display: block;
}
.navbar a:hover{
	color: red;
}
.side-nav{
	height: 100%;
	position: fixed;
	top: 0;
	right: -1px;
	background-color: #5987aa;
	transition: .1s;
}
#open-side{
	margin: 0px 10px;
	border: 0px solid;
	background: transparent;
	position: absolute;
	font-size: 60px;
	visibility: visible !important;
}
#titulos{
	list-style: none;
	text-align: right;
	font-size: 70px;
	position: absolute;
	bottom: 80px;
	right: 0px;
}
.titulos{
	visibility: hidden;
}
.windows-nav{
	position: absolute;
	z-index: 0;
	top: 100px;

}
.ventana , .ventana2 {
	color: black;
	width: 210px; /*resolucion en pantalla grande seria 420 x 450*/
	height: 225px;
	position: absolute;
	top: 100px;
	visibility: visible !important;
}
.ventana{
	background: green;
}
.ventana2{
	z-index: 100;
	border: 6px solid;
	height: 225px;
	top: 105px;
	left: 10px;
}

#side-menu{
	width:15%;
	transition: .5s;
}
#titulos a{
	text-decoration: none;
}
#side-menu.active {
	width:85%;
	transition: .5s;
}
.titulos.active{
	visibility: visible;
}
#logoMobile.act {
	visibility: visible;
}
#logo.act {
	margin-left: -100px;
}